    transform: translateY(40px);
  }
  100% {
    opacity: 1;
  }
}

.card-timeline {
  margin-top: 30px;
  position: relative;
}

.card-timeline:after {
  background: linear-gradient(
    to top,
    rgba(134, 214, 243, 0) 0%,
    rgba(81, 106, 204, 1) 100%
  );
  content: "";
  left: 42px;
  width: 2px;
  top: 0;
  height: 100%;
  position: absolute;
}

.card-item {
  position: relative;
  padding-left: 60px;
  padding-right: 20px;
  padding-bottom: 30px;
  z-index: 1;
}

.card-item:last-child {
  padding-bottom: 5px;
}

.card-item:after {
  content: attr(data-year);
  width: 10px;
  position: absolute;
  top: 0;
  left: 37px;
  width: 8px;
  height: 8px;
  line-height: 0.6;
  border: 2px solid #ffff;
  font-size: 11px;
  text-indent: -35px;
  border-radius: 50%;
  color: rgba(134, 134, 134, 0.7);
  background: linear-gradient(to bottom, #6277e3 0%, #516acc 100%);
}

.card-item-title {
  font-weight: 500;
  font-size: 14px;
  margin-bottom: 5px;
}

.card-item-desc {
  font-size: 13px;
  color: #ffff;
  line-height: 1.5;
  font-family: "DM Sans", sans-serif;
}

.card-contact-wrapper {
  margin-top: 20px;
}

.card-contact {
  display: flex;
  align-items: center;
  font-size: 13px;
  font-family: ropoto;
  line-height: 1.6;
  cursor: pointer;
}

.card-contact + .card-contact {
  margin-top: 16px;
}

.card-contact svg {
  flex-shrink: 0;
  width: 30px;
  min-height: 34px;
  margin-right: 12px;
  transition: 0.3s;
  padding-right: 12px;
  border-right: 1px solid #dfe2ec;
}

.contact-me {
  border: 0;
  outline: none;
  background: linear-gradient(
    to right,
    rgba(83, 200, 239, 0.8) 0%,
    rgba(81, 106, 204, 0.8) 96%
  );
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.15);
  color: #fff;
  padding: 12px 16px;
  width: 100%;
  border-radius: 5px;
  margin-top: 25px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  font-family: ropoto;
  transition: 0.3s;
}

.contact-me:hover {
  background: linear-gradient(
    to right,
    rgba(83, 200, 239, 1) 0%,
    rgba(81, 106, 204, 1) 96%
  );
}

.sidebar {
  height: auto;
  width: 250px;
  background-color: var(--third-color);
  top: 0;
  left: -250px;
  transition: 0.3s;
  z-index: 1000;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar ul li {
  padding: 15px 20px;
}

.sidebar ul li a {
  color: var(--primary-color);
  text-decoration: none;
  display: block;
  font-weight: bold;
}

.sidebar ul li a:hover {
  background-color: var(--primary-color);
  color: var(--third-color);
  padding: 5px;
  border-radius: 5px;
  transition: 0.3s;
}

.content {
  margin-left: 0;
  padding: 20px;
  transition: margin-left 0.3s;
  width: 100%;
}

.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0;
}

.dashboard {
  margin: 2rem 2rem 2rem 0;
}

.dashboard-sidebar {
  display: flex;
  gap: 20px;
}

.cards-dashboard {
  display: flex;
  gap: 20px;
  margin-bottom: 2rem;
  width: 100%;
}

.card-dashboard {
  background-color: var(--primary-color);
  border-radius: 8px;
  padding: 20px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-content-dashboard {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-text .amount {
  font-size: 24px;
  font-weight: bold;
  color: var(--third-color);
}

.card-text .description {
  margin-top: 8px;
  font-size: 14px;
  color: var(--third-color);
}

.card-icon .icon {
  font-size: 24px;
}

.growth.positive {
  color: #22c55e;
}

.growth.negative {
  color: #ef4444;
}

.icon.positive {
  color: #22c55e;
}

.icon.negative {
  color: #ef4444;
}

.messages-container {
  background-color: var(--primary-color);
  border-radius: 8px;
  padding: 20px;
  width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  margin-bottom: 2rem;
}

.messages-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.messages-header h2 {
  margin: 0;
}

.view-all {
  color: #8b949e;
  text-decoration: none;
  font-size: 14px;
}

.message {
  display: flex;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #202328;
  background-color: var(--bg-color2);
}

.message:last-child {
  border-bottom: none;
}

.avatar {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.message-content {
  display: flex;
  flex-direction: column;
}

.message-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.sender {
  font-weight: bold;
}

.time {
  font-size: 12px;
  color: var(--primary-color);
}

.message-text {
  color: var(--primary-color);
}

.table-container {
  background-color: var(--primary-color);
  border-radius: 8px;
  padding: 20px;
}

.table-container h2 {
  margin-top: 0;
  color: var(--third-color);
}

table {
  width: 100%;
  border-collapse: collapse;
  overflow-x: auto; /* Add horizontal scroll when necessary */
  white-space: nowrap; /* Prevent line breaks in table cells */
}

thead {
  background-color: #0d1117;
}

th,
td {
  padding: 12px;
  text-align: left;
}

tr:nth-child(even) {
  background-color: var(--bg-color);
}

tr:nth-child(odd) {
  background-color: var(--bg-color2);
}

.img-table {
  border-radius: 50%;
