.social-icons {
  display: flex;
}

.social-icons i,
.social i {
  color: var(--primary-color);
}

.social-icons a:hover {
  background-color: var(--primary-color);
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.2);
  transition: all ease 0.5s;
}
.social-icons a:hover i,
.social a:hover i {
  color: var(--third-color);
  transition: all ease 0.5s;
}

.copyright {
  color: var(--third-color);
  font-size: 15px;
}

.dashboard-post{
